<template>
<div>
  <button @click="getUserInfoClick">获取用户信息</button>
</div>
</template>

<script>
export default {
  name: 'Native_h5',
  created () {
    this.$bridge.registerhandler('testJavascriptHandler',function (res){
      console.log('sasa',res)
      alert('sasa:'+JSON.stringify(res))
    })
  },
  methods: {
    getUserInfoClick(e){
      console.log(e)
      // console.log(this.$bridge)
      this.$bridge.callhandler('Native',{'method': 'getAppInfo'},function (res){
        console.log(res)
        alert('getAppInfo :'+JSON.stringify(res))
      })
      // alert(this.$bridge.callhandler())
    }
  }
}
</script>

<style scoped>

</style>



